@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

:root{--montserrat: 'Montserrat', 'Open Sans', sans-serif;--aileron: 'Aileron', 'Open Sans', sans-serif;--accent: rgb(255, 50, 0);--accent-light: rgba(255, 50, 0, 0.85)}html,body{min-height:100%;padding:0;margin:0;box-sizing:border-box;background-color:#fcfcff;overflow-x:hidden;scroll-behavior:smooth}*{padding:0;margin:0;font-family:'Inter', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.container-fluid{width:87.5%}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;user-select:none}.section-content{min-height:50vh;padding-bottom:1vh;opacity:0;-webkit-animation:fadeIn 1s ease-in-out 1s forwards;animation:fadeIn 1s ease-in-out 1s forwards}.section-heading{text-transform:uppercase;font-family:'Montserrat', 'Open Sans', sans-serif;font-family:var(--montserrat);font-size:calc(1rem + 2vw);margin-top:2rem}.section-heading:hover hr{border-color:rgb(255, 50, 0);border-color:var(--accent)}@media (min-width: 801px){.section-heading{-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:translateY(-10vh);transform:translateY(-10vh);margin-top:15rem;padding-bottom:5rem}}@media (max-width: 800px){.section-heading{text-align:center}}.section-heading hr{margin:0.5rem 0;transition:0.3s}@media (max-width: 800px){.section-heading hr{margin-left:auto;margin-right:auto}}.section-img svg{opacity:0.5;width:20vw;max-width:10rem;height:auto;transition:0.3s ease-out}.section-img svg:hover,.section-img svg:focus{box-shadow:0 0.1rem 1rem #fff}@media (max-width: 800px){.section-img svg{width:10vw;max-width:3rem;margin:2vh auto}}@media (max-width: 768px){.container-fluid{width:100%}}@-webkit-keyframes fadeIn{from{visibility:hidden;opacity:0;-webkit-filter:saturate(0);filter:saturate(0)}to{visibility:visible;opacity:1;-webkit-filter:saturate(1);filter:saturate(1)}}@keyframes fadeIn{from{visibility:hidden;opacity:0;-webkit-filter:saturate(0);filter:saturate(0)}to{visibility:visible;opacity:1;-webkit-filter:saturate(1);filter:saturate(1)}}

.navbar{padding:1rem 1rem;background-color:#fcfcff;border-bottom:0.5px ridge transparent}.navbar-brand{text-transform:uppercase;color:rgba(0,0,0,0.9);font-weight:600;font-size:1.2rem;letter-spacing:-1px;-webkit-transform:scaleX(1.2);transform:scaleX(1.2)}.bar1,.bar2,.bar3{width:35px;height:2px;background-color:#333;margin:6px 0;transition:0.3s;-webkit-transform-origin:13.5px 0px;transform-origin:13.5px 0px}.bar1{-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-9px, 6px)}.bar2{width:30px;margin-left:5px;-webkit-transform:scaleX(0);transform:scaleX(0);opacity:0}.bar3{-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(-8px, -8px)}.collapsed .bar1{-webkit-transform:none;transform:none}.collapsed .bar2{opacity:1;-webkit-transform:scaleX(1);transform:scaleX(1)}.collapsed .bar3{-webkit-transform:none;transform:none}.nav-link{text-transform:uppercase;font-weight:400;font-size:1.1rem;color:rgba(0,0,0,0.6) !important;letter-spacing:-1px;text-align:center;-webkit-transform:scaleX(1.2);-moz-tranform:scaleX(1.2);transform:scaleX(1.2);transition:0.1s}.nav-link:not(:nth-last-of-type(1)){margin-right:5rem}.nav-link:hover{color:var(--accent-light) !important}.navbar-toggler{border:none}@media (max-width: 992px){.navbar-brand{margin-left:1rem}.navbar-collapse{padding-top:15vh}.nav-link{width:75%;margin:0 auto !important;padding:4vh 0.5rem}}

.hero{background-color:transparent;padding-top:calc(5rem + 10vh)}.hero__heading__text{opacity:1;-webkit-animation:fadeIn ease-out;animation:fadeIn ease-out;-webkit-animation-duration:1s;animation-duration:1s;font-size:calc(1rem + 5vw);text-transform:uppercase;line-height:0.8;overflow:hidden;font-weight:600;padding-left:0;margin-left:0;letter-spacing:-0.5vw;-webkit-transform-origin:0;transform-origin:0;-webkit-transform:scaleX(1.5);transform:scaleX(1.5)}@media (max-width: 800px){.hero__heading__text{letter-spacing:-1vw;font-size:calc(1.75rem + 5vw)}}.hero__arrow{-webkit-animation:fadeIn ease-in;animation:fadeIn ease-in;-webkit-animation-duration:0.5s;animation-duration:0.5s;margin:calc(2rem + 5vh) 0vh;position:relative}.hero__arrow__icon{-webkit-transform-origin:top;transform-origin:top;opacity:0.8;width:5vw;transition:0.3s}.hero__arrow__icon:hover{-webkit-transform:translateY(0.5rem);transform:translateY(0.5rem)}@media (max-width: 800px){.hero__arrow__icon{width:10vw;text-align:center}}

.about__wrapper{background:red}.about__heading{text-transform:uppercase;font-family:var(--montserrat);font-size:calc(1rem + 2vw)}.about__heading:hover hr{border-color:var(--accent)}@media (min-width: 801px){.about__heading{-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:translateY(-10vh);transform:translateY(-10vh)}}@media (max-width: 800px){.about__heading{text-align:center}}.about__heading hr{margin:0.5rem 0;transition:0.3s;max-width:20rem}@media (max-width: 800px){.about__heading hr{margin-left:auto;margin-right:auto}}.about__text{width:100%;font-size:1.25rem;padding-top:3vh;padding-bottom:2vh}.about__text a{color:#003264}@media (min-width: 801px){.about__text{padding-left:2vw;-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:translateY(-10vh);transform:translateY(-10vh)}}.about__profile{position:relative;-webkit-mask-image:linear-gradient(to bottom, #000 90%, rgba(0,0,0,0) 99%);mask-image:linear-gradient(to bottom, #000 90%, rgba(0,0,0,0) 99%)}@media (min-width: 801px){.about__profile{-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:translateY(-10vh);transform:translateY(-10vh)}}.about__profile__img{-webkit-filter:saturate(1);filter:saturate(1);width:35vw;max-width:30rem;height:auto;transition:0.3s ease-out}.about__profile__img:hover,.about__profile__img:focus{box-shadow:0 0.1rem 1rem #fff}@media (max-width: 800px){.about__profile__img{width:75vw;margin:2vh auto}}.about__subheading{opacity:0.8}.about__icons{width:100%;justify-content:center;flex-wrap:wrap;margin-top:2rem}.about__icons .skill-icon{margin-left:0.5rem;margin-right:0.5rem;margin-bottom:1rem}.about__icons svg{font-size:5rem}@media (max-width: 800px){.about__icons svg{font-size:3rem}}

.fade-in-section{opacity:0;visibility:hidden;transition:opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;transition:opacity 0.6s ease-out, transform 1.2s ease-out;transition:opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;will-change:opacity, visibility}.fade-in-section.is-visible{opacity:1;-webkit-transform:none;transform:none;visibility:visible}

.work__text{width:100%;font-size:1.25rem;padding-top:3vh;padding-bottom:2vh}.work__text a{color:#003264}@media (min-width: 801px){.work__text{padding-left:2vw;-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:translateY(-10vh);transform:translateY(-10vh)}}.work__subheading{opacity:0.8}.work__icons{width:100%;justify-content:center;flex-wrap:wrap}.work__icons .skill-icon{margin-left:0.5rem;margin-right:0.5rem}.work__icons svg{font-size:5rem}@media (max-width: 800px){.work__icons svg{font-size:3rem}}

.contact{padding-bottom:2rem}.contact__text{width:100%;font-size:1.5rem;padding-top:3vh;padding-bottom:2vh;display:flex;text-align:center;justify-content:center}.contact__text a{color:#111;margin-left:1rem;margin-right:1rem;opacity:0.75}.contact__text a:hover{opacity:1;cursor:pointer}@media (min-width: 801px){.contact__text{padding-left:2vw;font-size:4rem;-webkit-transform-origin:bottom;transform-origin:bottom}}.contact__subheading{opacity:0.8}.contact__icons{width:100%;justify-content:center;flex-wrap:wrap}.contact__icons .skill-icon{margin-left:0.5rem;margin-right:0.5rem}.contact__icons svg{font-size:5rem}@media (max-width: 800px){.contact__icons svg{font-size:3rem}}

